import Header from "../components/Header.jsx";
import {lazy, useCallback, useEffect, useMemo, useState} from "react";
import {urlBuilder} from "../additional.js";
import {getLastVersion} from "../any/versions.jsx";
import HolidaysTimerLogistics from "../components/HolidaysTimerLogistics.jsx";

const Filter = lazy(() => import("../components/Filter"));

export default function TeachersScreen() {
    const [filter, setFilter] = useState(`ALL`);
    const [list, setList] = useState([]);

    const onChangeFilter = (value) => setFilter(value);
    const getTeachers = useCallback(() => {
        let data = [];
        if (filter !== 'ALL') data = list.filter(item => {
            console.log(item);
            return item.types.find(item2 => item2.title === filter);
        });
        if (filter === 'ALL') data = list;
        return data.sort((a, b) => a.title < b.title ? -1 : (a.title > b.title ? 1 : 0))
    }, [filter, list])

    useEffect(() => {
        async function getData() {
            try {
                const request = await fetch(`/data/teachers-data.json?v=${getLastVersion()}`);
                const response = await request.json();
                setList(response.filter(item => item.active));
            } catch (error) {
                console.log('error')
            } finally {
                //
            }
        }

        getData();

    }, [])
    const getTitleLesson = useMemo((lessonCode) => {
        if(lessonCode === 'MATHEMATICS') return `Математика`;
        if(lessonCode === 'INFORMATICS') return `Информатика`;
        if(lessonCode === 'PHYSICS') return `Физика`;
        return null;
    }, [])

    return (
        <div className="wrapper-content">
            <HolidaysTimerLogistics/>
            <Header title={`Учителя`}/>
            <main className="teachers">
                <Filter items={[
                    {value: `ALL`, title: `ВСЕ`},
                    {value: `MATHEMATICS`, title: `Математики`},
                    {value: `INFORMATICS`, title: `Информатики`},
                    {value: `PHYSICS`, title: `Физики`}
                ]} active={filter} onChange={onChangeFilter}/>
                <div className="teachers-list">
                    {getTeachers()?.length > 0 && getTeachers().map((item, index) => (
                        <div key={index} className="teachers-list-item">
                            <div className="teachers-list-item-image">
                                <div>
                                    <img src={urlBuilder(item.image)} alt="photo"/>
                                </div>
                            </div>
                            <div className="teachers-list-item-title">
                                {item.title}
                                {(typeof item?.description !== "undefined" && item.description.trim().length !== 0) && (
                                    <div className="teachers-list-item-description">

                                        {item.types.length > 0 && item.types.map((type, indexType) => (

                                            <div key={indexType}
                                                 className={`teachers-list-item-description-classes ${type.title}`}>
                                                <p>{type.title === `MATHEMATICS` ? `Математика` : `Информатика`}</p>
                                                {type.classes?.length > 0 && type.classes.map(
                                                    (typeClass, indexTypeClass) => {
                                                        const title = getTitleLesson(typeClass);
                                                        if(title) return (
                                                            <strong key={indexTypeClass}>{title}</strong>

                                                        )
                                                    }
                                                )}
                                            </div>
                                        ))}
                                    </div>
                                )}

                            </div>
                        </div>
                    ))}
                    {getTeachers()?.length === 0 && (
                        <p>Список пуст</p>
                    )}
                </div>
            </main>
        </div>
    )
}